import React, {Component,useState} from 'react';
import {Alert,Card,CardImg,CardImgOverlay,CardText,CardBody,CardTitle, Button, Collapse,Row,Col,Container} from 'reactstrap';
import { Link } from 'react-router-dom';
import Carousel from "react-elastic-carousel";

import {DETAILS}  from '../shared/details'
import {withGetScreen} from 'react-getscreen'
import OutsideClickHandler from 'react-outsideclickhandler';
import image from "../assets/placehold.jpeg"
import GridLayout from 'react-grid-layout';



class Menu extends Component{
  constructor(props){
    super(props);
    this.state = {
      selectedTile:null,
      details:DETAILS
      // open:false
    }
    // this.close=this.close.bind(this);
  }

  onTileSelect(detail){
    this.setState({selectedTile:detail});
    this.props.close(this.props.id)
  }

  // close(){
  //   this.setState({
  //     open:!this.state.open
  //   });
  // }

  renderTile = (detail) => {
    if(detail!=null){
      if (this.props.isMobile()||this.props.isTablet() ){
        return(
          <div >
            <Alert color="danger" isOpen={this.props.open[this.props.id]}>
              Download <a href="#" className="alert-link">The Gaming Project App</a> to play on your smartphones.
            </Alert>
          </div>
        );
      } else {
        return(
          <Collapse isOpen={this.props.open[this.props.id]}>
            <Card className="ml-5" >
            
              <div className="mb-2">
                <Button onClick={() => this.props.close()}>X</Button>
                <CardImg top src={image} alt={detail.name} />
                <CardBody>
                  <CardTitle className="font-weight-bolder">{detail.name}</CardTitle>
                  <CardText >{detail.description}</CardText>
                </CardBody>
              </div>
              
            </Card>
          </Collapse> 
        );
      }
    }
    else{
      return(
        <div></div>
      );
    }
  }

  render() {
   
    const breakPoints = [
      { width: 1, itemsToShow: 5 },
      { width: 550, itemsToShow: 1 },
      { width: 768, itemsToShow: 4 },
      { width: 1200, itemsToShow: 4},
    ];
    const menu = this.props.details.map(detail=> {
      if (this.props.isMobile()){
        return (
          <Col className="col-12 center" style={{marginBottom:"20px"}}>
            {/* <div  onClick={() => this.props.close()}> */}
              <Card key={detail.id} onClick={() => this.onTileSelect(detail)}>
                <CardImg src={image}/>
                <CardImgOverlay>
                  <CardTitle className="font-weight-bolder">{detail.name}</CardTitle>
                </CardImgOverlay>
              </Card>
           
          </Col>
        );
      } else if (this.props.isTablet()){
        return ( 
          <Row>
            {/* <div  onClick={() => this.props.close()}> */}
              <Card key={detail.id} onClick={() => this.onTileSelect(detail)}>
                <CardImg src={image}/>
                <CardImgOverlay>
                  <CardTitle className="font-weight-bolder">{detail.name}</CardTitle>
                </CardImgOverlay>
              </Card>
            {/* </div> */}
          </Row>
        );
      } else {
        return (  
          <Col className="col-12 center" >
          
            <div>  
              <Col className="col-12 center" >
                <div key={detail.id} onClick={() => this.onTileSelect(detail)} className="mb-5 mt-1">
                  
                    <CardImg src={image} width="100" onClick={() => this.props.close()} className="p-0"/>
                    <CardImgOverlay>
                      <CardTitle className="font-weight-bolder">{detail.name}</CardTitle>
                    </CardImgOverlay>
                  
                </div>
              </Col>
              
            </div>
          </Col>
        );
      }
    });
    if (this.props.isMobile()||this.props.isTablet() ){
      return(
        <div className="row">
          <Container className="container-fluid">
            {this.renderTile(this.state.selectedTile)}
            <Row>
              {menu}
            </Row>
          </Container>
        </div>
      );
    } else {
      return(
        
        <div className="row container">
        
      
            
            <Carousel breakPoints={breakPoints}  renderPagination={({ pages, activePage, onClick }) => {
              return (
                <div> </div>
              )
            }} >
              {menu} 
               </Carousel> 
          
          
            <div  className="col-12 center">
            {this.renderTile(this.state.selectedTile)}
          </div>  
          
         
        </div>
       
      );
    }
  }
}

export default withGetScreen(Menu);